document.addEventListener("DOMContentLoaded", function(event) { 
    var bLazy = new Blazy({
        breakpoints: [{
            width: 420, src: 'data-src-small'
        }], 
        success: function(element){
            setTimeout(function(){
                var parent = element.parentNode;
                parent.className = parent.className.replace(/\bloading\b/,'');
            }, 200);
        }
   });

    var image = new Image();
    image.src = "../images/logo.jpg";
    image.onload = function () {
        // setInterval(function(){ glitchme(image); }, 1000);
    }

function glitchme(){
    var seed_num = Math.floor(Math.random() * (30 - 1) + 1);
    var iters = Math.floor(Math.random() * (3 - 1) + 1);

    glitch( { seed: seed_num, quality: 100, iterations: iters, amount:     1  } )
        .fromImage( image )
        .toDataURL()
        .then( function( dataURL ) {
            var glitchedImg = new Image();
            glitchedImg.src = dataURL;
            glitchedImg.id="logo_menu_image";
            var elements = document.getElementById('logo_menu');
            glitchedImg.style.marginTop = "35px";
            elements.replaceChild(glitchedImg, document.getElementById('logo_menu_image'));
    } );
}

});